export class Perceptron {
    weights: any;
    theta: number;
    id: number;
    constructor(id_param) {
        this.weights = [];
        for (var i = 0; i < 35; i++) { this.weights.push(0); }
        this.theta = 0;
        this.id = id_param;
        return this;
    }
    sum(inputs) {
        let s = 0;
        for (var i = 0; i < this.weights.length; i++) { s += (this.weights[i] * inputs[i]); }
        return s;
    }
    activate(s) {
        return (s > this.theta) ? 1 : 0;
    }
    train(samples, max_itterations) {
        var total_error = 0, itterations = 0;
        do {
            total_error = 0;
            ++itterations;
            for (var i = 0; i < samples.length; i++) {
                var output = this.activate(this.sum(samples[i].data));
                var target = (this.id === samples[i].target_number) ? 1 : 0;
                var error = target - output; total_error += Math.abs(error);
                for (var j = 0; j < this.weights.length; j++) { 
                    var delta_w = 0.25 * (target - output) * samples[i].data[j];
                    this.weights[j] += delta_w;
                }
                this.theta += 0.25 * (target - output) * -1;
            }
        } while(total_error != 0 && itterations < max_itterations);
    }
}


